.App {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  font-family: 'Architects Daughter';
  font-size: 15px;
  text-align: center;

  background-image: url(./background.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  position: fixed;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
}

.Currency{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  background-color: white;
  opacity: 0.6;

  width: 75%;
  height: fit-content;
  padding: 1rem;

  border-radius: 1rem;
  color: black
}

#amount{
  background-color: white;

  width: 50%;
  padding-top: 2rem;
  padding-bottom: 0.5rem;
  margin: 1rem 0;

  font-size: 1.5rem;
  font-family: "Architects Daughter";
  text-align: center;
  color: grey;

  border: none;
  border-bottom: 2px solid rgb(6, 101, 179);
}

.dropDown{
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 40%;
  margin: 1rem;

  font-size: 1.5rem;
}

.container{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.custom-select select{
  -webkit-appearance: none;
  -moz-appearance: none;
  border: none;
  padding: 0.5rem;
  width: fit-content;
  background-color: transparent;
  color: black;
  font-family: 'Architects Daughter';
  font-weight: bold;
  text-align: center;
  appearance: none;
}

.custom-select::after{
  content: "\25bc";
  font-size: 1rem;
}

.compute{
  margin: 1rem;
  padding: 0.5rem;
  width: 20%;

  font-family: 'Architects Daughter';
  font-weight: bolder;
  font-size: 1.2rem;

  background-color: black;
  border: none;
  color: white
}

.converted{
  display: none;
  font-size: 1.5rem;
  font-weight: bolder;
}